.headerSection {
  
  background: url(../../../images/header-bg-mob.png), black;
background-repeat: no-repeat;
background-position: 50% 50%;
// block-size: cover;
height: 640px;

  padding-top: 32px;
  @media (min-width: 1440px) {
    background: #111619;
    height: 930px;
    }
}
.headerTop {
  width: 91%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  
 
}
.headerTitle {
    color: #fff;
    font-family: Crimson Text;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 140.1%;
    @media (min-width: 1020px) {
        font-size: 40px;
      }
}

.headerNav{
  display: flex;
}

.modalShowButton {
  background-color: transparent;
  border-color: transparent;
  @media (min-width: 768px) {
      display: none;
    }
}

.headerIcon {
    width: 32px;
    height: 19px;
    @media (min-width: 768px) {
        display: none;
      }
}
.headerNavList{
  display: flex;
  @media (max-width: 767px){
    display: none;
  }

}
.headerNavList > li + li {
  margin-left: 121px;
}
.headerNavLink{
  color: #FFF;
    font-family: Anonymous Pro;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 140.1%;
    cursor: pointer;
      transition: 500ms;
    
      &:hover,
      &:focus {
        color: #FF3F3F;
        background: rgba(0, 0, 0, 0.301);
        border-radius: 5px;
        
      }
   
}
.container{
    margin-top:138px ;
  padding-left: 16px ;
  padding-right: 16px;
  @media (min-width: 767px) {
    padding-left: 80px;
     
    }
}
.container p + p,
.container button
 {
    margin-top: 64px
    ;
}
.container p:nth-child(1){
    color: #FFF;
        font-family: Anonymous Pro;
        font-size: 32px;
        font-style: normal;
        font-weight: 400;
        line-height: 140.1%;
        @media (min-width: 767px) {
            font-size: 64px;
          }


}
.container p:nth-child(2){
    color: #FFF;
        font-family: Anonymous Pro;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 140.1%;
        @media (min-width: 767px) {
            font-size: 40px;
          }

        
}
.container span{
color: #FF3F3F;
}
.container button {
    padding: 8px 48px;
    border: 1px solid #FFF;
    color: #FFF;
    background: transparent;
        font-family: Anonymous Pro;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: 140.1%;
        cursor: pointer;
        transition: 250ms;
        
          &:hover,
          &:focus {
            color:#FF3F3F;
            background: rgba(0, 0, 0, 0.301);
            border: 1px solid #e9e9e910;
          }
        @media (min-width: 767px) {
            padding: 16px 80px;
            font-size: 40px;
          }
       
}
.heroThumb{
    padding-left: 16px;
      padding-right: 16px;
  display: flex;
  justify-content: space-between;
  @media (min-width: 1439px) {
      padding-top: 131px;
      
        padding-right: 80px;
    }
}
.heroThumb > img{
  @media (max-width: 1439px) {
      display: none;
     
      
    }
}

// form----------------------------
.form {
  margin-top: 24px;
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 399px;
  gap: 12px;

  @media screen and (min-width: 768px) {
    width: 399px;
  }
}

.label {
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.32px;

  color: gray;
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 769px) {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 1.32px;
  }

  &:hover,
  &:focus {
    color: rgb(22, 22, 22);
  }
}

.input {
  width: 255px;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.32px;
  padding: 4px 6px;
  border-radius: 6px;
  border: 1.295px solid #000;
  
  box-shadow: 1.29px 1.29px 13px 0px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 1.29px 1.29px 13px 0px rgba(0, 0, 0, 0.25);
  margin-top: 4px;
  
  outline: none;
  cursor: text;

  &::placeholder {
    color: #9a9a9a;
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 0.88px;
  }

  @media screen and (min-width: 768px) {
    width: 100%;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.88px;
    padding: 13px 15px;
    margin-top: 8px;

    &::placeholder {
      font-size: 16px;
      font-weight: 500;
      letter-spacing: 0.88px;
    }
  }

  &:hover,
  &:focus {
    box-shadow: 1.294952630996704px 1.294952630996704px 12.949525833129883px 0px rgba(251, 174, 7, 0.25);
  }
}



.btn {
  color: #fff;
  font-size: 12px;
  letter-spacing: 1.14px;
  border-radius: 32px;
  border: none;
  background: #000;
  padding: 12px 20px;
  cursor: pointer;
  margin-top: 50px;

  @media screen and (min-width: 768px) {
    width: 255px;
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 1.14px;
    padding: 20px 31px;
    
  }

  &:hover,
  &:focus {
    color: rgb(168, 164, 164);
    background-color: rgb(82, 80, 80);
  }
}

.closeIcon {
  // fill: #000;
  width: 30px;
  height: 30px;

  &:hover,
  &:focus {
    scale: 1.4;
  }
}

.closeModal{
  background-color: transparent;
  border-color: transparent;
  margin-left:120px;
  margin-bottom: 15px;
}